

























































.page {
    display: flex;
    justify-content: flex-end;
}

.cus-btn {
    color: #2E4AF7;
    font-size: 14px;
    padding-left: 25px;
    cursor: pointer;
    line-height: 22px;
    position: relative;

    &:first-child {
        padding-left: 0;
    }
}

.table-btns {
    cursor: pointer;
    display: flex;
    padding: 0 10px;
    justify-content: space-between;
    align-items: center;

    .edit-btn:hover {
        color: #2E4AF7
    }

    .delete-btn:hover {
        color: #E6A23C
    }

    .center {
        width: 100%;
    }

}


/deep/ .active-row {
    background-color: #F5F7FA;
}
